import React, { Dispatch, SetStateAction } from "react"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { IconButton } from "@rp/react/web/mui/core"
import { ZoomIn } from "@rp/react/web/mui/icons"

type PDFViewerHeaderZoomInButtonProps = {
  scale: number
  setScale: Dispatch<SetStateAction<number>>
  color?: string
}

function PDFViewerHeaderZoomInButton({ scale, setScale, color }: PDFViewerHeaderZoomInButtonProps) {
  return (
    <RPTooltip title="Zoom in" arrow placement="bottom">
      <span>
        <IconButton
          data-testid="zoom-in-button"
          onClick={() => setScale((prevScale: number) => prevScale + 0.2)}
          disabled={scale >= 2}
        >
          <ZoomIn sx={{ color }} />
        </IconButton>
      </span>
    </RPTooltip>
  )
}

export { PDFViewerHeaderZoomInButton }
